import React, { useState } from "react";
import "./Item.css";

import angleDown from "../../../assets/icons/angle-down.svg";
import angleUp from "../../../assets/icons/angle-up.svg";

const Item = (props) => {
	const [toggle, setToggle] = useState(true);
	return (
		<>
			<div className="skill__type">
				<img src={props["icon-title"]} alt="" className="icon__type" />

				<div className="skill__type_title">
					<h3 className="title__skill">{props.title}</h3>
					<span className="subtitle__skill">
						Less than {props.experience}{" "}
						{props.experience > 1 ? "years" : "year"}
					</span>
				</div>
				<div className="toggle">
					<a
						className="angle__button"
						href={`#${props.id}`}
						onClick={() => setToggle(!toggle)}
					>
						{toggle ? (
							<img
								src={angleDown}
								alt=""
								className="icon__angle"
							/>
						) : (
							<img src={angleUp} alt="" className="icon__angle" />
						)}
					</a>
				</div>
			</div>
			<div
				className={`skill__item ${toggle ? "active" : ""}`}
				id={props.id}
			>
				{props.skills.map((item, index) => (
					<div className="technology" key={index}>
						<img src={item.icon} alt="" className="icon" />
						<p className="skill__technology">{item.name}</p>
					</div>
				))}
			</div>
		</>
	);
};

Item.defaultProps = {
	skills: [],
};

export default Item;
